/* components/Home.css */
.home-page {
    padding: 20px 0;
  }
  
  .home-page h1 {
    text-align: center;
    margin-bottom: 40px;
    color: var(--header-color);
  }
  
  .gauges-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
  }
  
  .gauge-wrapper {
    background-color: var(--card-bg);
    border-radius: 8px;
    padding: 15px;
    box-shadow: var(--card-shadow);
    flex: 1;
    min-width: 250px;
    max-width: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid var(--border-color);
  }
  
  .gauge-wrapper h3 {
    margin-top: 0;
    margin-bottom: 15px;
    color: var(--header-color);
    text-align: center;
  }
  
  @media (max-width: 768px) {
    .gauges-container {
      flex-direction: column;
      align-items: center;
    }
  }
  